.admin-container {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.logo {
  width: max-content;
}

.admin-input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  color: #333;
  font-weight: 300;
}

.admin-button {
  width: 100%;
  padding: 12px;
  background: white;
  color: black;
  border: 1px solid #ccc;
  border-radius: 16px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 400;
}

.admin-button:hover {
  background-color: #f2f2f2;
}

.admin-table {
  width: 100%;
  border-collapse: collapse;
}

.admin-table th, .admin-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
}

.admin-table th {
  background-color: #f2f2f2;
}
@media (max-width: 470px){
  .logo {
      width: 297px;
  }
}